import {Component, AfterViewInit, OnInit} from '@angular/core';
import {API} from '@aws-amplify/api';
import {ApiConfig} from '../shared/api.config';

@Component({
  templateUrl: './dashboard.component.html'
})
export class DashboardComponent implements AfterViewInit, OnInit {
  subtitle: string;

  things: any = null;
  thingsChartLabels: string[] = ['Online', 'Offline'];
  thingsChartLabels1: string[] = ['Active', 'Inactive'];
  thingsChartColors: string[] = ['#06d79c', '#6c757d'];
  thingsChartValues: number[] = [0, 0];
  thingsChartValues1: number[] = [0, 0];
  thingsChartHasData = false;

  constructor() {
    this.subtitle = 'This is some text within a card block.';
  }

  ngAfterViewInit() {
  }

  ngOnInit(): void {
    API.get(ApiConfig.apiName, ApiConfig.paths.pathThings, ApiConfig.params)
      .then(response => {
        const now = new Date().getTime();
        this.things = response.data.things;
        let onlineCount = 0;
        let offlineCount = 0;
        let activeCount = 0;
        let inactiveCount = 0;
        for (const thing of this.things) {
          if (thing['status'] === 'OFFLINE') {
            offlineCount++;
          } else {
            onlineCount++;
          }
          if (thing['lastUpdate'] !== undefined && now - thing['lastUpdate'] < 60 * 60 * 1000) {
            activeCount++;
          } else {
            inactiveCount++;
          }
        }
        this.thingsChartValues = [onlineCount, offlineCount];
        this.thingsChartValues1 = [activeCount, inactiveCount];
        this.thingsChartHasData = true;
        console.log(this.things);
      })
      .catch(error => {
        console.log(error.response);
      });
  }
}
